import { Component, Host, h, Element, Prop } from '@stencil/core';
import { TStatusValue } from './status-utils';
import { injectCSS } from '../../utils';
import { createStyles } from './status-styles';

@Component({
  tag: 'dc-ui-status',
  shadow: true,
})
export class DcUiStatus {
  @Element() el: HTMLElement;
  @Prop() status: TStatusValue = 'positive';

  render() {
    injectCSS(this.el, createStyles, this.status);

    return (
      <Host>
        <dc-ui-text variant="mid-size-text">
          <slot></slot>
        </dc-ui-text>
      </Host>
    );
  }
}
