import {
  color as colorUtils, getCss,
} from "../../utils";
import {TStatusValue} from "./status-utils";


export const createStyles = (
  status: TStatusValue
): string => {

  const styles = getCss({
    '@global': {

      'dc-ui-text::before': {
        content: '""',
        display: 'inline-block',
        width: '8px',
        height: '8px',
        marginRight: '8px',
        marginBottom: '1px',
        borderRadius: '100%',
        backgroundColor: {
          'positive': colorUtils.green02,
          'negative': colorUtils.red02,
        }[status],
      }
    }
  });

  return styles;
};
